.IngredientButton {
  background-color: rgb(241, 215, 184);
  color: rgb(66, 57, 47);
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  margin: 10px;
  display: inline-flexbox;
  flex-flow: row;
  width: 80px;
  border: 1px solid #AA6817;
}

.IngredientButton img{
  background-color: transparent;
  height: 30px;
  width: 30px;
  display: block;
  outline: none;
  margin: auto;
  padding-bottom: 10px;
}

.Success {
  color: #5C9210;
}

.Danger {
  color: #944317;
}

